  /*NAV STYLE*/
  .nav-bar {
    height: 80px;
    width: 100%;
    background-color: rgb(35, 66, 153);
    display: flex;
    flex-direction: row;
    align-items: center;
    color: white;
  }
  .nav-nav{
    margin: auto;
  }
  .nav-bar img {
    width: 60px;
    padding-right: 10px;
  }
  .nav-bar > div {
    display: flex;
  }
  
  .nav-container {
    color: white;
    position: absolute;
    top: 0px;
    left: 0px;
    margin: 0px;
    height: 50px;
    display: flex;
    width: 100%;
    text-align: center;
    align-items: center;
    justify-content: center;
    background: rgb(50, 80, 200);
  }
  
  .nav-container p {
    text-align: center;
    font-size: 1rem;
  }
  .nav-items {
    display: flex;
    justify-content: space-between;
    list-style: none;
  }
  .nav-logo {
    height:100%;
  }
  .nav-items > li {
    padding: 10px;
  }
  
  .bg {
    margin: 0px;
    padding: 0px;
  }
  
  /* MEME GENERATOR */
  
  .meme-container {
    margin: auto;
    margin-top: 50px;
    width: 75%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
  }
  .meme-form{
    display: flex;
    justify-content: center;
    align-items: center;
  }
.in-meme{
  display:flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  width: 50%;
  
}
.in-meme input{
  margin: 10px 5px;
  padding: 10px;
  width: 100%;
}
  .meme-input {
    display: flex;
    flex-direction: row;
    width: 100%;
  }
  .meme-button, button, .download {
    background-color: rgb(35, 66, 153);
    color: white;
    width: 50%;
    padding: 10px;
    margin: auto;
    margin-top: 20px;
    text-decoration: none;
    text-align: center;
    border-radius: 5px;
  }


  
  .meme-button,
  input, .download, button {
    border-radius: 5px;
    border: 1px solid #d5d4d8;
  }
  input {
    text-indent: 10px;
  }
  /* JOKE */
  
  .joke-container {
  }
  
  /* messages */
  
  .bg span {
    color: blue;
  }
  .img-container {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: max-content;
    height: fit-content;
    margin-top: 2rem;
    width: fit-content;
  }
  .meme-img {
    max-width: 100%;
    border-radius: 3px;
  }
  
  .meme--text {
    position: absolute;
    width: 95%;
    text-align: center;
    left: 50%;
    transform: translateX(-50%);
    margin: 15px 0;
    padding: 0 5px;
    font-family: impact, sans-serif;
    font-size: 2em;
    text-transform: uppercase;
    color: white;
    letter-spacing: 1px;
    text-shadow: 2px 2px 0 #000, -2px -2px 0 #000, 2px -2px 0 #000,
      -2px 2px 0 #000, 0 2px 0 #000, 2px 0 0 #000, 0 -2px 0 #000, -2px 0 0 #000,
      2px 2px 5px #000;
  }
  
  .top {
    top: 0;
  }
  .bottom {
    bottom: 0;
  }
  


  

  /*Footer ========*/

.footer{
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    margin: auto;
    margin-top:1vh;
    position: absolute;
    bottom: -50;
    width:100%;
    padding: 2rem 0rem 2rem 0rem;
  
  }
  
  .footer hr{
    width: 35%;
    margin:auto;
    margin-bottom: 1rem;
  }
  .footer span{
    font-size: 20px;
    color: black;
    padding: 10px;
  }
  
  .footer span:hover{
    color: rgb(35, 66, 153);
    opacity: 1;
  }
  .footer p{
    margin: 1rem 0rem 0.5rem 0rem;
    font-size: 12px;
  }
  .footer div{
    margin:auto;
  }
  
  
  
  @media (min-width:200px) and (max-width: 450px) {
    .meme-container {
      width:90vw;
      margin: auto;
    }
    .meme-button, .meme-input,.download, button{
      width:90vw
    }
    .meme--text{
      font-size: 1.6rem;
    }
    .in-meme{
      width:100%;
    }

  }

  @media (min-width: 451px) and (max-width: 890px) {
    .meme-container{
      width:100%;
    }
    .in-meme{
      width:100%;
    }
    .meme-input{
      width: 100%;
    }
    .meme-button, .download, button{
      width:90%;
    }
    .in-meme input{
      width: 90%
    }
    .img-container{
      width: 90%;
    }
    .meme-img{
      width: 100%;
    }
  }